<template>
    <div class="signIn">
      <div class="header">
        <span @click="back" class="back">
          <img src="../../assets/img/back.png">
        </span>
        <span>签到中心</span>
        <span class="back"></span>
        <span class="tips" @click="showTips">说明</span>
      </div>
      <div class="signSection">
          <div>
            <v-progress type="circle" class="circle1" default="test" :percentage="num" stroke-linecap="round" track-color="#fff"></v-progress>
          </div>
          <div class="cover">
            <button @click="sign" class="signBtn" :class="{clicked: canClick !== '0'}">{{ canClick === '0' ? '领取哼哈值' : '今日已签到'}}</button>
          </div>
          <div class="num">
              <div class="numCount">{{ henghazhi }}</div>
              <div class="numTitle">当前哼哈值</div>
          </div>
          <div class="switch">
              <mt-switch class="mtSwitch" v-model="val" @change="setNotice"></mt-switch>
              <span class="signNotice">签到提示</span>
          </div>
          <ul class="days">
              <li class="day" style="bottom: 2.3rem;left: calc(50% - 3rem);">第一天</li>
              <li class="day" style="bottom: 3.5rem;left: calc(50% - 3.5rem);">第二天</li>
              <li class="day" style="bottom: 4.7rem;left: calc(50% - 3rem);">第三天</li>
              <li class="day" style="bottom: 5.9rem;left: calc(50% - 0.75rem);">第四天</li>
              <li class="day" style="bottom: 4.7rem;right: calc(50% - 3rem);">第五天</li>
              <li class="day" style="bottom: 3.5rem;right: calc(50% - 3.5rem);">第六天</li>
              <li class="day" style="bottom: 2.3rem;right: calc(50% - 3rem);">第七天</li>
          </ul>
      </div>
      <div class="content">
          <div class="title">福利任务</div>
          <ul class="missions">
              <li class="mission" v-for="(mission, index) in missions" :key="index">
                  <span>
                      <span class="detail">{{ mission.title }}</span>
                      <br>
                      <span class="reward">{{ mission.reward }}</span>
                  </span>
                  <button class="missionBtn" @click="todo(index)">去完成</button>
              </li>
          </ul>
      </div>
    </div>
</template>

<script>
import { getUserSignState, userSignIn } from '../../api/getdata'
import { Switch, Toast } from 'mint-ui'
import Progress from 'vue-multiple-progress'
export default {
  name: 'signIn',
  data () {
    return {
      num: '',
      henghazhi: '',
      val: false,
      canClick: '',
      signCounts: null,
      missions: [{
        title: '每日发布动态', reward: '获得2哼哈值', state: 0, linkTo: '/home/community/release'
      }, {
        title: '每日不间断浏览网页10分钟', reward: '获得5哼哈值', state: 0, linkTo: '/home/index'
      }, {
        title: '每日在线修炼30分钟', reward: '获得10哼哈值', state: 0, linkTo: '/home/prictice'
      }, {
        title: '充值豪礼', reward: '每充值10元赠100哼哈值哦~', state: 0, linkTo: '/home/index'
      }, {
        title: '完善资料', reward: '获得20哼哈值', state: 0, linkTo: 'home/mine/myprofile'
      }]
    }
  },
  mounted () {
    this.getSignState()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    sign () {
      if (this.canClick === '0') {
        userSignIn().then(res => {
          console.log(res)
          if (res.status === '0') {
            this.canClick = '1'
            this.num += 12.5
            if (this.num > 75) {
              this.num = 0
            }
            if (this.signCounts === 6) {
              this.henghazhi += 50
            } else {
              this.henghazhi += 10
            }
            Toast({
              message: '签到成功',
              duration: 1500
            })
          }
        })
      }
    },
    getSignState () {
      getUserSignState().then(res => {
        console.log(res)
        this.signCounts = res.data.signcount
        this.henghazhi = res.data.F_Point
        if (this.signCounts === 0) {
          this.num = 0
        } else {
          this.num = (this.signCounts - 1) * 12.5
        }
        this.canClick = res.data.signStatus
      })
    },
    setNotice () {
      console.log(this.val)
    },
    showTips () {
      this.$router.push('/home/mine/henghaTips')
    },
    todo (index) {
      console.log(index)
      this.$router.push(this.missions[index].linkTo)
    }
  },
  components: {
    'v-progress': Progress,
    'mt-switch': Switch
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .tips {
    font-size: 0.42667rem;
    position: absolute;
    right: 0;
  }

  .signSection {
    position: fixed;
    top: 1.5rem;
    width: 100%;
    height: 7.0667rem;
    background: #ff7e7e;
    overflow: hidden;
  }

  .circle1 {
    margin-top: 2rem;
    position: relative;
    transform: rotate(225deg);
  }

  .circle2 {
    width: 3.6rem;
    height: 3.6rem;
    border-radius: 50%;
    border: 0.2rem solid #fff;
    position: absolute;
    left: -0.1rem;
    top: -0.1rem;
  }

  .cover {
    width: 100%;
    height: 2.55rem;
    position: absolute;
    left: 0;
    bottom: 0;
    background: #ff7e7e;
    z-index: 9;
  }

  .signBtn {
    width: 3.2rem;
    height: 1.04rem;
    border-radius: 0.52rem;
    border: none;
    outline: none;
    background: #fff;
    color: #ff7e7e;
    margin-top: 0.64rem;
    font-size: 0.4rem;
  }

  .clicked {
    color: #c0c0c0;
  }

  .num {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    left: calc(50% - 1rem);
    bottom: 2.5rem;
    position: absolute;
    z-index: 10;
    background: #ff7e7e;
  }

  .numCount {
    font-size: 0.8rem;
    color: #fff;
  }

  .numTitle {
    font-size: 0.37333rem;
    color: #fff;
  }

  .switch {
    position: absolute;
    right: 0.4rem;
    top: 0.4rem;
    z-index: 10;
  }

  .mtSwitch {
    display: flex;
    justify-content: center;
  }

  .signNotice {
    font-size: 0.37333rem;
    color: #fff;
  }

  .day {
    list-style-type: none;
    position: absolute;
    z-index: 10;
    color: #fff;
    font-size: 0.37333rem;
    width: 1.5rem;
  }

  .content {
    width: calc(100% - 1.12rem);
    margin: 0 auto;
    color: #666666;
    margin-top: 9rem;
  }

  .title {
    text-align: start;
    font-size: 0.42667rem;
    margin-left: 0.21333rem;
    position: relative;
    z-index: -1;
    margin-top: 0.37333rem;
    color: #333333;
  }

  .title::before {
    content: '';
    display: block;
    position: absolute;
    width: 0.05333rem;
    height: 100%;
    left: -0.21333rem;
    top: 0;
    background: #ff7e7e;
  }

  .missions {
    list-style: none;
    margin-top: 0.2rem;
  }

  .missions li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: start;
    padding: 0.2rem 0;
    border-bottom: 1px solid #f5f5f5;
  }

  .missionBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.89333rem;
    height: 0.75rem;
    border-radius: 0.375rem;
    font-size: 0.34667rem;
    outline: none;
    background: #fff;
    border: 1px solid #ff7e7e;
    color: #ff7e7e;
  }

  .detail {
    display: inline-block;
    font-size: 0.37333rem;
    color: #333333;
  }

  .reward {
    display: block;
    font-size: 0.29333rem;
    color: #666666;
  }
</style>



